@import url('https://fonts.googleapis.com/css2?family=Abril+Fatface&display=swap');
.navbar {
    width: 100%;
    height: auto;
    display: flex;
    justify-content: space-around;
    align-items: center;
    background-color: var(--first-color);
    padding: 5px;
    top: 0%;
    left: 0%;
    position: fixed;
    padding: 10px;
    z-index: 1110;
}

.imgBx  {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  color: white;
}
.imgBx h1{
    font-size: 50px;
    line-height:20px;
    letter-spacing: -4px;
    font-family: 'Abril Fatface', serif;

}
.imgBx h1 span:nth-child(2){
    font-size: 30px;
}
.imgBx p{
    font-size: 10px;
}
.nav ul {
    display: flex;
}

 ul li {
    margin: 0px 10px;
    text-decoration: none;
    color: white;
    cursor: pointer;

}

.clr-icon {
    font-size: 34px;
    color: white;
}
.active li{
    color:black;
  }
 
  .menu{
      position: absolute;
      display: flex;
      display: none;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    top: 10px;
    right: 20px;
    width: 35px;
    height: 40px;
    cursor: pointer;
  }
  .menu span{
    height: 5px;
    background-color: aliceblue;
    width: 100%;
    margin: 3px;
    border-radius: 30px;
  }



  @media (max-width:800px) {
    .navbar {
        justify-content: space-between;
        padding: 10px 40px;
    }
  }
  @media (max-width:600px) {
    .navbar {
        padding: 10px 20px;
        flex-direction: column;
        justify-content: start;
        align-items: self-start;
        text-align: start;
    }
    .menu{
        display: flex;
    }
    .nav{
        width: 100%;
    }
    .nav ul {
        display: none;
        flex-direction: column;
        text-align: center;
    }
    .nav ul.open {
        display: flex;
       
    }
    .nav ul li{
        margin: 10px;
    }
  }